import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/AboutView.vue'),
  },
  {
    path: '/service',
    name: 'service',
    component: () => import('../views/ServiceView.vue'),
    children: [
      {
        path: 'outsourcing',
        name: 'outsourcing',
        component: () => import('../views/service/OutsourcingView.vue'),
      },
      {
        path: 'enterprise',
        name: 'enterprise',
        component: () => import('../views/service/EnterpriseView.vue'),
      },
      {
        path: 'firewall',
        name: 'firewall',
        component: () => import('../views/service/FirewallView.vue'),
      },
      {
        path: 'rwd',
        name: 'rwd',
        component: () => import('../views/service/RwdView.vue'),
      },
      {
        path: 'design',
        name: 'design',
        component: () => import('../views/service/WebdesignView.vue'),
      },
      {
        path: 'hosting',
        name: 'hosting',
        component: () => import('../views/service/HostingView.vue'),
      },
      {
        path: 'network',
        name: 'network',
        component: () => import('../views/service/NetworkView.vue'),
      },
    ],
  },
  {
    path: '/news',
    name: 'news',
    component: () => import('../views/NewsView.vue'),
  },
  {
    path: '/show',
    name: 'show',
    component: () => import('../views/ShowView.vue'),
  },
  {
    path: '/connection',
    name: 'connection',
    component: () => import('../views/ConnectionView.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
